import { useUserStore } from '/@/store/modules/user';

export enum Api {
  LIST_STATION_TREE_URL = '/baseset/common/list-orgStationTree',
  LIST_STATION_PERIOD_URL = '/baseset/period/list',
  LIST_TYPE_INDEX_URL = '/baseset/assessrule/index/typeIndexList',
  CREATE_URL = '/po/assess/template/create',
  UPDATE_URL = '/po/assess/template/update',
  UPDATE_BUSINESS_STATUS_URL = '/po/assess/template/updateBusinessStatusToInvalid',
  DELETE_URL = '/po/assess/template/delete',
  UPLOAD_FILE_URL = '/po/assess/template/upload-file',
  EXPORT_EXCEL_URL = '/po/assess/template/export-excel',
  GET_URL = '/po/assess/template/get',
  DUPLICATE_URL = '/po/assess/template/duplicate',
  PAGE_URL = '/po/assess/template/page',
  LIST_DETAIL_URL = '/po/assess/template/detail/list',
}

// 获取电站树
export const getTreeList = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_TREE_URL,
      params,
    },
    {},
  );

// 获取电站分期
export const getStationPeriod = (params) =>
  defHttp.get(
    {
      url: Api.LIST_STATION_PERIOD_URL,
      params,
    },
    {},
  );

// 获取细则指标
export const getTypeIndexList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_TYPE_INDEX_URL,
      params,
    },
    {},
  );

// 添加细则模板
export const createData = (params) =>
  defHttp.post(
    {
      url: Api.CREATE_URL,
      params,
    },
    {},
  );

// 修改细则模板
export const updateData = (params) =>
  defHttp.put(
    {
      url: Api.UPDATE_URL,
      params,
    },
    {},
  );

// 修改细则模板业务状态
export const updateBusinessStatus = (params) =>
  defHttp.put(
    {
      url: Api.UPDATE_BUSINESS_STATUS_URL,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return createData(params);
  }
  return updateData(params);
};

// 删除细则模板
export const deleteData = (params) =>
  defHttp.delete(
    {
      url: Api.DELETE_URL,
      params,
    },
    {},
  );

// 导出细则模板
export const exportExcel = (params) =>
  defHttp.post(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
      responseType: 'blob',
    },
    { isOnlyResult: true },
  );

// 上传细则模板文件
export const uploadFile = (params) =>
  defHttp.post(
    {
      url: Api.UPLOAD_FILE_URL,
      params,
    },
    {},
  );

