export const columns = (): BasicColumn[] => {
  const result = [
    {
      title: '单据编号',
      dataIndex: 'billCode',
      sorter: true,
      resizable: true,
      width: 200,
    },
    {
      title: '所属公司',
      dataIndex: 'stationOrganName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '电站名称',
      dataIndex: 'stationName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '期数',
      dataIndex: 'periodName',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '主题',
      dataIndex: 'subject',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '监管单位',
      dataIndex: 'regulatoryUnitName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '考核单位',
      dataIndex: 'assessmentUnitName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '考核生效时间',
      dataIndex: 'effectiveDate',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '创建人',
      dataIndex: 'personMemberName',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '创建时间',
      dataIndex: 'fillinDate',
      sorter: true,
      resizable: true,
      width: 150,
      customRender: ({ text }) => {
        return useRender.renderDate(text);
      },
    },
    {
      title: '审批状态',
      dataIndex: 'status',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: ({ text, record }) => {
        return useRender.renderFlowStatus(text, record.statusText);
      },
    },
    {
      title: '业务状态',
      dataIndex: 'businessStatus',
      sorter: true,
      resizable: true,
      customRender: ({ record }) => {
        return record.businessStatusText;
      },
    },
  ];
  if (useUserStore().isMultiOrganization) {
    return result as BasicColumn[];
  } else {
    const [one, , ...others] = result;
    return [one, ...others] as BasicColumn[];
  }
};

